import moment from 'moment';

export const getElectronicMonitoringTick = (item) => {
    return `\n${item.decisionNumber} \n      ${moment().format('YYYY-MM-DD HH:mm:ss')} \n    ${item.holder} \n  ${item.licenceNo} \n  ${item.cllx} \n  ${item.plateNumber} \n  ${item.illegalPlace} \n  ${item.wfnr} \n  ${item.cljg} \n  ${item.fines} \n    0 \n      ${item.score} \n---------------------------------------------- \n \n1.24 \n2.153%`;
};

export const getDriverChangeTick = (item) => {
    return ` \n  ${item.lsh} \n${item.businessType} \n${item.dabh} \n${item.certNumber} \n    ${item.partyName} \n${moment().format('YYYY-MM-DD HH:mm:ss')} \n${item.blbm} \n---------------------------------------------- \n \n`;
};

export const getIdNumberTick = (item) => {
    return ` \n  ${item.slh}\n${item.slyy}\n    ${item.partyName}\n${item.certNumber}\n${item.dwmc}\n  ${item.cbr}\n${moment().format('YYYY-MM-DD HH:mm:ss')}\n${item.lzfs} \n---------------------------- \n \n1.15\n2.`;
};

export const getIDPhotoTick = (item) => {
    return `\n    ${item.certNumber}\n${moment().format('YYYY-MM-DD HH:mm:ss')}\n--------------------------------\n\n\n\n\n\n\n\n\n\n`;
};

export const getCarTick = (item) => {
    return `\n${item.lsh}\n  ${item.businessTypeName}\n  ${item.hphm}\n${item.clsbdh}\n  ${item.certNumber}\n      ${item.partyName}\n  ${moment().format('YYYY-MM-DD HH:mm:ss')}\n    ${item.jbr}\n----------------------------\n\n\n`;
};
